@import url(//fonts.googleapis.com/css?family=Inconsolata);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.bg-darker-1 {
  background-color: rgba(0, 0, 0, 0.05); }

.bg-darker-2 {
  background-color: rgba(0, 0, 0, 0.1); }

.bg-darker-3 {
  background-color: rgba(0, 0, 0, 0.15); }

.bg-darker-4 {
  background-color: rgba(0, 0, 0, 0.2); }

.bg-darker-5 {
  background-color: rgba(0, 0, 0, 0.25); }

.bg-lighter-1 {
  background-color: rgba(255, 255, 255, 0.05); }

.bg-lighter-2 {
  background-color: rgba(255, 255, 255, 0.1); }

.bg-lighter-3 {
  background-color: rgba(255, 255, 255, 0.15); }

.bg-lighter-4 {
  background-color: rgba(255, 255, 255, 0.2); }

.bg-lighter-5 {
  background-color: rgba(255, 255, 255, 0.25); }

html, body, #root, .app {
  height: 100%;
  width: 100%; }

a {
  color: #ce9178; }

h1 {
  color: #4EC9B0; }

h2 {
  color: #569CD6; }

h3 {
  color: #b669a3; }

h4 {
  color: #edeb85; }

h5 {
  color: #4EC9B0; }

h6 {
  color: #569CD6; }

.light.app-sidebar {
  background-color: white; }

.app {
  display: flex;
  height: 100%;
  flex-direction: row; }
  .app-sidebar {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.8); }
    .app-sidebar-item {
      font-size: 30px;
      min-width: 5%;
      padding: 10px;
      display: block;
      text-align: center; }
      .app-sidebar-item-icon {
        color: rgba(255, 255, 255, 0.7); }
        .app-sidebar-item-icon:hover {
          color: rgba(255, 255, 255, 0.9);
          cursor: pointer; }
    .app-sidebar-tools {
      flex-grow: 1;
      display: flex;
      flex-direction: column-reverse; }
  .app-main {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%; }
    .app-main-header {
      flex: 0 0 auto;
      background-color: rgba(0, 0, 0, 0.8);
      color: rgba(255, 255, 255, 0.8); }
      .app-main-header-title h1 {
        padding: 0 0 0 10px;
        font-family: "Inconsolata";
        color: rgba(255, 255, 255, 0.8); }
    .app-main-content {
      flex: 1 1 auto;
      position: relative;
      overflow-y: auto;
      overflow-x: hidden;
      padding: 0 15px 40px 15px;
      background-color: rgba(0, 0, 0, 0.875);
      font-family: "Inconsolata";
      font-size: 20px;
      font-weight: inherit;
      line-height: 1.6;
      color: white;
      flex-wrap: wrap; }
      .app-main-content pre, .app-main-content code {
        font-family: "Inconsolata";
        color: #edeb85;
        font-weight: bolder; }
      .app-main-content pre {
        background-color: rgba(0, 0, 0, 0.5); }
    .app-main-footer {
      flex: 0 0 auto;
      min-width: 100%;
      background-color: rgba(0, 0, 0, 0.85);
      border-top: 1px solid rgba(255, 255, 255, 0.15); }
      .app-main-footer-header {
        color: rgba(255, 255, 255, 0.6);
        background-color: rgba(0, 0, 0, 0.1);
        display: flex;
        flex-direction: row;
        padding: 8px 20px 0 1em; }
        .app-main-footer-header-tabs {
          font-size: 15px;
          border-bottom: 1px solid rgba(255, 255, 255, 0.15);
          color: rgba(255, 255, 255, 0.95); }
        .app-main-footer-header-tools {
          flex-grow: 1;
          display: flex;
          flex-direction: row-reverse;
          font-size: 20px; }
          .app-main-footer-header-tools-trash:hover {
            color: rgba(255, 255, 255, 0.9);
            cursor: pointer; }

.blog-page-preview {
  padding: 5px 0; }

.blog-post-preview {
  border: 2px solid white;
  border-radius: 5px;
  padding: 10px; }

/* Let's get this party started */
::-webkit-scrollbar {
  width: 6px; }

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.15);
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  border-radius: 10px; }

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.15); }

::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(255, 255, 255, 0.2); }

